* {
  margin: 0;
}
.wrapper {
  height: 100vh;
  display: flex;
  background: linear-gradient(#2a00b7, #42006c) no-repeat;
}

.container {
  width: 500px;
  display: flex;
  flex-direction: column;
  margin: auto;
  padding: 0 30px;
  background: #fff;
  border-radius: 14px;
}
.header {
  width: 100%;
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 9px;
}
.text {
  color: #3c009d;
  font-size: 48px;
  font-weight: 700;
}

.input {
  width: 400px;
  height: 80px;
  display: flex;
  align-items: center;
  margin: 55px auto 0;
  background: #eaeaea;
  border-radius: 6px;
}
.input img {
  margin: 0 30px;
}

.input input {
  height: 50px;
  width: 400px;
  background: transparent;
  border: none;
  outline: none;
  color: #797979;
  font-size: 19px;
  font-weight: 700;
  color: #3c009d;
}
.passwordBtn {
  border: none;
  outline: none;
  background: transparent;
  padding: 0;
  margin-left: -15px;
  cursor: pointer;
}

.submitContainer {
  display: flex;
  gap: 30px;
  margin: 60px auto;
}
.submit {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 220px;
  height: 59px;
  color: #fff;
  background: #4c00b4;
  border-radius: 50px;
  font-size: 19px;
  font-weight: 700;
  cursor: pointer;
}
.error {
  margin-top: 5px;
  text-align: center;
  color: red;
  font-size: 20px;
  font-weight: 500;
}
.home-page {
  display: flex;
  flex-direction: column;
  margin: auto;

  color: #fff;
}
